<!--
 * @Autor: linxu
 * @Date: 2022-03-14 09:14:15
 * @LastEditors: linxu
 * @LastEditTime: 2022-06-01 15:17:26
 * @Description: 模板页
-->
<template>
  <div>
    <BMainTopComponent
      :isShowBackBtn="true"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    />
    <el-container>
      <el-main>
        <div class="crew-info">
          <toggle-crew></toggle-crew>
          <p class="content-title">模板页</p>
          <!-- 筛选 -->
          <div class="search-box">
            <div class="search-item">
              <span>机组编号:</span>
              <el-input placeholder="请输入机组编号" v-model="searchForm.num"> </el-input>
            </div>
            <div class="search-item">
              <span>电子围栏开启情况:</span>
              <el-select
                v-model="searchForm.type"
                :popper-append-to-body="false"
                placeholder="请选择电子围栏开启情况"
              >
                <el-option label="全部" value=""></el-option>
                <el-option label="开启" value="1"></el-option>
                <el-option label="关闭" value="2"></el-option>
              </el-select>
            </div>
            <div class="search-item">
              <span>维保策略录入情况:</span>
              <el-select
                v-model="searchForm.type1"
                :popper-append-to-body="false"
                placeholder="请选择维保策略录入情况"
              >
                <el-option label="全部" value=""></el-option>
                <el-option label="未录入" value="1"></el-option>
                <el-option label="已录入" value="2"></el-option>
              </el-select>
            </div>
            <div class="search-btn-box">
              <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
              <el-button class="query-btn" @click="queryTableData()">查 询</el-button>
            </div>
          </div>
          <!-- 操作按钮 -->
          <div class="operation-box">
            <el-button type="primary" class="add-btn" @click="handleAddProject()"
              >新建项目</el-button
            >
          </div>
          <!-- 表格 -->
          <TableList
            :options="options"
            :columns="columns"
            :dataSource="dataSource"
            :dataTotal="dataTotal"
            :pagination="paginationCfg"
            @handleChangePage="handleChangePage"
            @handleChangeSize="handleChangeSize"
            border
            v-loading="loading"
          >
            <el-table-column
              slot="operation"
              label="操作"
              align="center"
              v-bind="options && options.props"
            >
              <template slot-scope="scope">
                <div class="operate-group">
                  <el-button type="text" @click="handleEdit(scope.row)"> 编辑 </el-button>
                  <el-button type="text" @click="handleDelete(scope.row)"> 删除 </el-button>
                </div>
              </template>
            </el-table-column>
          </TableList>
        </div>
      </el-main>
      <!-- <toggle-crew ></toggle-crew> -->
    </el-container>
  </div>
</template>

<script>
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import moment from 'moment'

import ToggleCrew from '@/components/toggleCrew'
const contractColumns = (that) => [
  {
    label: '文件类型',
    prop: 'FileType',
    minWidth: '20%',
    formatter: (row, column, index) => {
      return row.FileType
        ? ProjectFileTypeEnum.find((item) => item.value == row.FileType).label
        : ''
    }
  },
  {
    label: '文件名称',
    prop: 'FileName',
    minWidth: '35%'
  },
  {
    label: '上传时间',
    prop: 'CreateTime',
    minWidth: '35%',
    formatter: (row, column, index) => {
      return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
    }
  },
  {
    label: '上传人',
    prop: 'CreateUserName',
    minWidth: '20%'
  },
  {
    label: '备注',
    prop: 'FileDetail',
    minWidth: '25%'
  }
]
// 服务器请求返回值
const creatArray = function (val) {
  let arr = []
  for (let i = 1; i <= val; i++) {
    arr.push(i)
  }
  return arr
}
const dataArr = creatArray(12)
const dataSourceFun = () => {
  return dataArr.map((item, index) => {
    const num = Math.ceil(Math.random() * 2)
    return {
      key: index,
      DisplayName: dataArr.length - index,
      CollectorToken: 10000 + index,
      num: 1200,
      limitPower: 400,
      isOpen: num,
      isWrite: num
    }
  })
}

export default {
  name: 'test',
  mixins: [paginationConfigMixin],
  components: {
    ToggleCrew
  },
  data() {
    return {
      loading: false,
      // 筛选条件
      searchForm: {
        name: '',
        type: '',
        type1: ''
      },
      // 表格
      columns: contractColumns(this),
      dataSource: [],
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 126 // 操作列宽度
        }
      },
      data: [],

      recordId: null, //操作行数据ID
      dialogVisible: false // 电子围栏弹窗
    }
  },
  mounted() {
    this.data = dataSourceFun()
    this.dataTotal = this.data.length
    this.queryTableData()
  },
  methods: {
    handleBackClick() {
      this.$router.push({ path: '/project/project-list' })
    },
    queryTableData() {
      this.loading = true
      console.log('searchForm------', this.searchForm)
      const arr = this.data.slice(
        (this.paginationCfg.currentPage - 1) * this.paginationCfg.pageSize,
        this.paginationCfg.currentPage * this.paginationCfg.pageSize
      )
      this.$set(this, 'dataSource', arr)

      this.loading = false
    },

    // 重置筛选条件
    reset() {
      this.$set(this, 'searchForm', {
        name: '',
        type: '',
        type1: ''
      })
    },

    // 编辑
    handleEdit(row) {
      console.log(row)
      this.recordId = row.key
      this.dialogVisible = true
    },

    // 删除
    handleDelete(row) {
      console.log(row)
      this.recordId = row.key
    },

    // 弹框组件关闭
    closeDialog(key) {
      this[key] = false
    }
  }
}
</script>

<style lang="less" scoped>
.crew-info {
  // display: initial !important;
  .project-info {
    .content-title {
      height: 48px;
      line-height: 48px;
    }

    .item-content {
      padding: 0 0 16px 8px;
    }
  }

  // .search-box {
  //   background-color: transparent;
  //   padding: 0 16px;
  //   height: 32px;
  //   border: 0;
  //   .search-item {
  //     padding: 0 24px 0 0;
  //     height: 32px;
  //   }
  // }
}
</style>



